import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive


// define a generatePDF function that accepts a tickets argument
const PrintNotaGambar = (
  row1name = "",
  row1isi = "",
  row2name = "",
  row2isi = "",
  row3name = "",
  row3isi = "",
  row4name = "",
  row4isi = "",
  username = "",
  tanggal = "",
  validby = "",
  tableColumn = [],
  title = "",
  tableRows,
  gambar = [],
  footer = []
) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  doc.setProperties({
    title: title,
  });
  const logo = 'data:image/png;base64,'

  doc.autoTable({
    head : tableColumn,
    body : tableRows,
    foot : [footer],
    startY: 42,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 20 },
    bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
        headStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fontSize : 8,
          fillColor: "#E8E8E8",
        },
        footStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fillColor: "#E8E8E8",fontSize : 8,
        },
  });
  let finalY = doc.lastAutoTable.finalY + 7;
  let finalX = 14;
  let spliter = 0;
  gambar.map((data) => {
    if(spliter % 4 === 0){
      if(spliter === 0 ){
        finalX = 14
      } else {
        finalY = finalY + 30;
        finalX = 14
      }
    }
    doc.addImage(data?.lokasi_gambar, "PNG", finalX, finalY, 40, 30);
    spliter = spliter + 1;
    finalX = finalX + 45;
    return true
  })

  doc.setFontSize(10);
  doc.addImage(logo, "PNG", 14, 15, 35, 12)
  //row 1
  doc.text(row1name, 14, 32);
  doc.text(":", 37, 32);
  doc.text(row1isi, 40, 32);
  doc.text(row2name, 120, 32);
  doc.text(":", 141, 32);
  doc.text(row2isi, 145, 32);
  //row 2
  doc.text(row3name, 14, 37);
  doc.text(":", 37, 37);
  doc.text(row3isi, 40, 37);
  doc.text(row4name, 120, 37);
  doc.text(":", 141, 37);
  doc.text(row4isi, 145, 37);

  //Stamp
  doc.text("User", 160, finalY + 35);
  doc.text(":", 168, finalY + 35);
  doc.text(username, 170, finalY + 35);
  doc.text("Cetak", 160, finalY + 40);
  doc.text(":", 168, finalY + 40);
  doc.text(tanggal, 170, finalY + 40);
  doc.text("Valid", 160, finalY + 45);
  doc.text(":", 168, finalY + 45);
  doc.text(validby, 170, finalY + 45);
  // doc.autoPrint();
  // doc.save(`${title}_${dateStr}.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>" +
    title +
    "</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
    string +
    "></embed></body></html>"
  );
  x.document.close();
};

export default PrintNotaGambar;
