import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reset } from "redux-form";
import Swal from "sweetalert2";
import { setListBarangBarcodeSet } from "../../actions/barangSetAction";
import { getListBarang } from "../../actions/kodeBarangAction";
import {
  finishSend,
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../../actions/LogicAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { getListPOPenjualan } from "../../actions/validasiPOAction";
import { gagal } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import {
  calculate_level2_packingList,
  calculate_level3_packingList,
  setFocus,
} from "../../helper";
import { renderField } from "../../renderField";

class FormBarangSet extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Jenis",
        },
        {
          dataField: "nama_jenis",
          text: "Nama Jenis",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "gross",
          text: "Gross",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() =>
                    this.handleSubmit(row)
                  }
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListPOPenjualan());
    this.props.dispatch(getListBarang());
  }

  pilihBarang(data, no_bon) {
    localStorage.setItem("no_bon_keranjang", no_bon);
    data.forEach((x) => {
      this.handleSubmit(x);
    });
  }

  async handleSubmit(data) {
    this.props.dispatch(progressSend());
    let param = data.kode_jenis;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    let kode_tukar_1 = 0;
    let kode_tukar_2 = 0;
    let kode_tukar_3 = 0;
    let customer_1 = localStorage.getItem("customer_1")
    let customer_2 =
      this.state.level_customer === "LEVEL2"
        ? this.props.kode_customer
        : localStorage.getItem("customer_2");
    let customer_3 =
      this.state.level_customer === "LEVEL3" ? this.props.kode_customer : "-";
      await AxiosMasterGet(
        `kelompok-harga/get/by-jenis-and-customer/${customer_1}&${data.kode_jenis}&PUSAT`
      )
        .then((res) => (kode_tukar_1 = res.data.kode_tukar_normal || 0))
        .catch(() => (kode_tukar_1 = 0));
    await AxiosMasterGet(
      `kelompok-harga/get/by-jenis-and-customer/${customer_2}&${data.kode_jenis}&PUSAT`
    )
      .then((res) => (kode_tukar_2 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_2 = 0));
    await AxiosMasterGet(
      `kelompok-harga/get/by-jenis-and-customer/${customer_3}&${data.kode_jenis}&PUSAT`
    )
      .then((res) => (kode_tukar_3 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_3 = 0));
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kode_jenis}`)
      .then((res) => {
        var simpan_nota_1 =
          JSON.parse(localStorage.getItem("nota_penjualan_level1")) || [];
        var simpan_nota_2 =
          JSON.parse(localStorage.getItem("nota_penjualan_level2")) || [];
        var simpan_nota_3 =
          JSON.parse(localStorage.getItem("nota_penjualan_level3")) || [];
        var nota_gambar = JSON.parse(localStorage.getItem("nota_gambar")) || [];
        var posisi = simpan_nota_1.findIndex(
          (list) => list.kode_kategori === data.kode_kategori
        );
        var posisi_2 = simpan_nota_2.findIndex(
          (list) => list.kode_kategori === data.kode_kategori
        );
        var posisi_3 = simpan_nota_3.findIndex(
          (list) => list.kode_kategori === data.kode_kategori
        );
        nota_gambar.push({ lokasi_gambar: res.data?.image });
        localStorage.setItem("nota_gambar", JSON.stringify(nota_gambar));
        const bruto = parseFloat(data.gross) - (parseFloat(data.pkg))
        console.log("KODE TUKAR", kode_tukar_1, "BRUTO" , bruto);
        if (posisi !== -1) {
          var nota_1 = {
            netto_disc: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_1)) / 100 -
              (parseFloat(data.disc) * parseFloat(bruto || 0)) / 100 +
              parseFloat(simpan_nota_2[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(bruto || 0) * parseFloat(kode_tukar_1)) / 100 ||
                0) + parseFloat(simpan_nota_2[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(0 === undefined ? 0 : 0) +
              parseFloat(simpan_nota_2[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc) / 100) * parseFloat(bruto || 0) +
              parseFloat(simpan_nota_2[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(bruto || 0) +
              parseFloat(simpan_nota_2[posisi].bruto), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_1,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          var nota_2 = {
            netto_disc: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_2)) / 100 -
              (parseFloat(data.disc) * parseFloat(bruto || 0)) / 100 +
              parseFloat(simpan_nota_2[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(bruto || 0) * parseFloat(kode_tukar_2)) / 100 ||
                0) + parseFloat(simpan_nota_2[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(0 === undefined ? 0 : 0) +
              parseFloat(simpan_nota_2[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc) / 100) * parseFloat(bruto || 0) +
              parseFloat(simpan_nota_2[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(bruto || 0) +
              parseFloat(simpan_nota_2[posisi].bruto), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_2,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          var nota_3 = {
            netto_disc: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_3)) / 100 -
              (parseFloat(data.disc) * parseFloat(bruto || 0)) / 100 +
              parseFloat(simpan_nota_3[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(bruto || 0) * parseFloat(kode_tukar_3)) / 100 ||
                0) + parseFloat(simpan_nota_3[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(0 === undefined ? 0 : 0) +
              parseFloat(simpan_nota_3[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc) / 100) * parseFloat(bruto || 0) +
              parseFloat(simpan_nota_3[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(bruto || 0) +
              parseFloat(simpan_nota_3[posisi].bruto), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_3,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          simpan_nota_1.push(nota_1);
          simpan_nota_2.push(nota_2);
          simpan_nota_3.push(nota_3);
          simpan_nota_1.splice(posisi, 1);
          simpan_nota_2.splice(posisi_2, 1);
          simpan_nota_3.splice(posisi_3, 1);
          localStorage.setItem(
            "nota_penjualan_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_penjualan_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_penjualan_level3",
            JSON.stringify(simpan_nota_3)
          );
        } else {
          var nota_1_else = {
            netto_disc: parseFloat(data.netto_disc || 0).toFixed(3),
            netto: parseFloat(data.netto || 0).toFixed(3), //murni
            ongkos_rp: parseFloat(
              0 === undefined ? 0 : 0
            ), //ongkos
            disc: data.disc || 0,
            bruto: parseFloat(bruto || 0).toFixed(3), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: data.kode_tukar,
            keterangan: data.keterangan,
            disc_persen: data.disc || 0,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          var nota_2_else = {
            netto_disc: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_2)) / 100 -
              (parseFloat(data.disc) * parseFloat(bruto || 0)) / 100
            ).toFixed(3),
            netto: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_2)) / 100 ||
              0
            ).toFixed(3), //murni
            ongkos_rp: parseFloat(
              0 === undefined ? 0 : 0
            ), //ongkos
            disc: (
              (parseFloat(data.disc) / 100) *
              parseFloat(bruto || 0)
            ).toFixed(3),
            bruto: parseFloat(bruto || 0), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_2,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          var nota_3_else = {
            netto_disc: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_3)) / 100 -
              (parseFloat(data.disc) * parseFloat(bruto || 0)) / 100
            ).toFixed(3),
            netto: (
              (parseFloat(bruto || 0) * parseFloat(kode_tukar_3)) / 100 ||
              0
            ).toFixed(3), //murni
            ongkos_rp: parseFloat(
              0 === undefined ? 0 : 0
            ), //ongkos
            disc: (
              (parseFloat(data.disc) / 100) *
              parseFloat(bruto || 0)
            ).toFixed(3),
            bruto: parseFloat(bruto || 0), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_3,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          simpan_nota_1.push(nota_1_else);
          simpan_nota_2.push(nota_2_else);
          simpan_nota_3.push(nota_3_else);
          localStorage.setItem(
            "nota_penjualan_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_penjualan_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_penjualan_level3",
            JSON.stringify(simpan_nota_3)
          );
        }
        var simpan = {
            netto_disc: (
                (parseFloat(bruto || 0) * (parseFloat(kode_tukar_1)))  / 100
            ).toFixed(3),
          gross: parseFloat(data.gross || 0).toFixed(3) || 0, //berat + bungkus
          netto: (
            (parseFloat(bruto || 0) * parseFloat(kode_tukar_1)) / 100 ||
            0
          ).toFixed(3), //murni
          qty: 1 || 1,
          k: parseFloat(data.k || 0),
          ongkos_rp: parseFloat(
            0 === undefined ? 0 : 0
          ), //ongkos
          kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
          disc_persen: 0,
          kadar_jual: parseFloat(kode_tukar_1 || 0), //harga%
          kadar_level2: res.data.level2 || 0,
          kadar_level3: res.data.level3 || 0,
          kadar_modal: hasil[0].kadar_beli || 0, //harga%
          kode_jenis: hasil[0].kode_jenis || 0, //kode_jenis
          kode_barcode: "-",
          kode_barcode_set: "-",
          p: parseFloat(data.p || 0),
          disc:  0,
          pkg: parseFloat(data.pkg || 0),
          bruto: parseFloat(bruto || 0).toFixed(3), //berat
          harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
        };
        var simpan_level2 = calculate_level2_packingList({
          data: data,
          hasil: hasil,
          res: res,
        });
        var simpan_level3 = calculate_level3_packingList({
          data: data,
          res: res,
          hasil: hasil,
        });

        var array =
          JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
        var array_level2 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level2")) || [];
        var array_level3 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level3")) || [];
        array.push(simpan);
        array_level2.push(simpan_level2);
        array_level3.push(simpan_level3);
        var GrandTotalNetto = array
          .map((list) => parseFloat(list.netto_disc))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
        localStorage.setItem(
          "transaksiPenjualan_level2",
          JSON.stringify(array_level2)
        );
        localStorage.setItem(
          "transaksiPenjualan_level3",
          JSON.stringify(array_level3)
        );
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(finishSend());
        this.props.dispatch(getListTransaksiPenjualan());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormTransaksiPenjualan"));
        setFocus("discGlobal_penjualan");
        this.props.dispatch(getNetto("transaksiPenjualan"));
        this.setState({
          disc: 0,
          GrandTotalNetto: (
            parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
          ).toFixed(3),
        });
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }

  getSumData() {
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
  }
  handleBarcode = (data) => {
    console.log("MANGGIL");
    clearTimeout(this.delay);
    this.delay = setTimeout(() => {
      let hasil = data.target.value;
      console.log(hasil);
      if (hasil.length === 14) {
        AxiosMasterGet(
          "barang-set/get-by-kode-barcode-set-active/" + hasil
        ).then((res) => {
          this.props.dispatch(setListBarangBarcodeSet(res.data.detail_barang))
        });
        return true;
      } else {  
        // let split = hasil.split("X");
        // this.props.change("kodeBarang", split[0]);
        // this.setState({
        //   kodeJenis: split[0],
        //   kadarCetak: split[1],
        //   kadarJual: split[2],
        // });
      }
    }, 1500);
  };

  render() {
    return (
      <>
        <div className="col-lg-12 col-md-12">
          <div className="row">
          <div className="col-md-3 col-lg-4">
            <Field
              name="kode_barcode"
              label="Kode Barcode Set"
              type="text"
              component={renderField}
              onChange={(data) => this.handleBarcode(data)}
            />
          </div>
          </div>
          <GlobalTabel
            keyField="no_preorder"
            data={this.props.list_barang}
            columns={this.state.columns}
            textEmpty="Data Timbangan Kosong"
          />
        </div>
      </>
    );
  }
}

export default connect((state) => {
  return {
    listPO: state.provinsi.listPO,
    kodeBarang: state.provinsi.kodeBarang,
    list_barang : state.barcodeSet.list_barang
  };
})(FormBarangSet);
